footer{
    background: $primary;
 
}
.footer{
    position: relative;
    padding-top: 120px;
    padding-bottom: 54px;
    line-height: 16px;
    font-size: 12px;
    color: white;
    display: flex;
    justify-content: space-evenly;
    @media (max-width : 992px) {
        flex-direction: column;
        align-items: center;
        
    }
    &__col{
        flex: 1;
        @media (max-width : 992px) {
            line-height: 30px;
        }
    }
    &__left{
        text-align: left;
    }
    &__center{
        text-align: center;
    }
    &__right{
        text-align: right;
    }
    &__logo{
        height: 124px;
        width: 124px;
        position: absolute;
        transform: translateX(-50%);
        left: 50%;
        top: -64px;
    }    
}