#gallery{
    padding-top: 80px;
    padding-bottom: 80px;
}
.choices{
margin-top: 24px;
justify-content: center;
display: flex;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 24px;
letter-spacing: 0px;
text-align: left;
h2{
        padding-left: 10px;
        padding-right: 10px;
    
    }
.active{
    font-weight: 700;
}


}
.grid{
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    @media (min-width : 776px) {
        grid-template-columns: 1fr 1fr;
        
    }
    @media (min-width : 992px) {
        grid-template-columns: 1fr 1fr 1fr;
        
    }
    &__box{
        width: 100%;
        padding-top: 75%;
        background: blueviolet;
        min-height: 100px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        img{
            transition : all ease-in-out .3s;
            &:hover{
                width: 110%;
                height: 110%;
            }
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;           
            object-fit: cover;
        }
    }
}
.gallery-btn{
    background: $primary;
    align-self: center;
    padding: 10px 20px ;
    border: inherit;
    color: white;
    border-radius: 50px;
    padding : 18px;
    cursor: pointer;
    transition: all ease-in-out .3s ;
    border: solid 1px $primary;
    &:focus{
        border-radius: 50px;
        outline: none;
    }
    &:hover{
        color: $primary;
    background: $secondary;
    }
        .text{
            width: 150px;
            font-weight: 700;
        }
}